<template>
    <div>
        <ul class="movie-list" >
            <li @click="goDetail(movie.id)" class="movie" v-for="movie in movieList" :key="movie.id" >
                <div class="movie-img"><img :src="movie.img" /></div>
                <div class="movie-info">
                    <p>{{movie.nm}}</p>
                    <p>{{movie.ver}}</p>
                    <p>主演: {{movie.star}}</p>
                    <p>{{movie.showInfo}}</p>
                </div>
            </li> 
            <div class="loading" v-show = "loadingShow">
              <img src="@/assets/img/loading.gif">
            </div> 
            <div class="tip" v-show="tip">
              <h4>--你已到达世界尽头!--</h4>  
            </div>         
        </ul>          
    </div>
</template>

<script>
import Axios from "axios";

export default {
  data() {
    return {
      movieList: [], //接收来自api的数据 或 分页数据
      loadingShow: false, //加载动画
      tip: false, //<h4>--你已到达世界尽头!--</h4>
    }
  },
  methods:{
    loadData(){
      //url1 => api接口
    /*  let url1 = API_PROXY +
        "http://m.maoyan.com/movie/list.json?type=hot&limit=10&offset=" +
        this.moveList.length; */
      let url2 = "/static/data/moviedata.json"; //url2 => 本地数据.

      Axios.get(url2)
        .then(res => {
          let list = res.data.data.movies;
          let data = list.slice(
                this.movieList.length, 
                this.movieList.length +3 ); //获取api数据.

          if(data.length < 3){ //最后一页如果不足<5或0时,为"世界尽头"
            this.tip = true;
          }
          this.movieList = this.movieList.concat(data);
        })
        .catch(() => {
          alert("数据接收失败!");
        });
    },
    goDetail(movieId){
      this.$router.push('/movie/movieDetail/' + movieId);
    }
  },
  mounted(){
    this.loadData();
     // window.onload = function(){ //注意: gif加载无法实现
    window.onscroll = () => {
      this.loadData();
      let clientHeight = document.documentElement.clientHeight;//可视高度
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;; //动态高度
      let scrollHeight = document.documentElement.scrollHeight; //整体高度
      //console.log(clientHeight, scrollTop, scrollHeight);
      if( scrollTop+clientHeight >= scrollHeight-110){ //110是底部导航的高度.
       // console.log('这里是世界尽头: '+this.tip, this.loadingShow);
        this.loadingShow = true; //当前页数据获取完毕后加载动画
        if(!this.tip){
          this.loadData();   
        }else{
          this.loadingShow = false;
        }
       
      }
    }
  } 
};

/**
 * 问: assets和static的区别?
共同点: 二者都可以存放静态文件.用于webpack的配置
不同点:
1).assets中的文件用于webpack打包使用.
assets中的文件可以有变化,
且引用assets中的文件时,使用的是相对路径.
2).static中的文件webpack则是原封不动地copy,static是用于存放不变化的静态文件的.且引用static中的文件时,使用绝对路径.
 */
</script>

<style scoped>
.movie-list {
  margin: 1rem 0;
}
.movie {
  display: flex;
  padding: 0.2rem;
  border-bottom: 1px solid #ccc;
}
.movie-img {
  /*flex: flex-grow(放大) flex-shrink(缩小)  flex-basis */
  flex: 1 1 0;
  margin-right: 0.2rem;
}

.movie-info {
  flex: 2 1 0;
}
.movie-info p:first-child {
  font-weight: bold;
  color: #cd0200;
}
.loading {
  text-align: center;
  margin-bottom: 1rem;
}
.tip{
  text-align: center;
}
/*
****movieList.vue中经典布局: 实现文字部分是图片部分的二倍***
/*flex: flex-grow(放大) flex-shrink(缩小)  flex-basis */
/*
.movie-img { flex: 1 1 0; }
.movie-info { flex: 2 1 0;}
*/
</style>


