<template>
<div class="albums">
    <aplayer autoplay :list="songs" :showLrc="isShow" 
    :music="{
        title: 'secret base 好听吗!',
        artist: 'Silent Siren',
        src: 'https://moeplayer.b0.upaiyun.com/aplayer/secretbase.mp3',
        pic: 'https://moeplayer.b0.upaiyun.com/aplayer/secretbase.jpg',
        lrc: 'https://api.bzqll.com/music/netease/lrc?key=579621905&id=526307800'
    }" 
    />
    
</div>
</template>

<script>
import Aplayer from 'vue-aplayer'
import Axios from "axios"

export default {
    data() {
        return {
            songs: [],
            isShow: false,
        }
    },
    components: {
          Aplayer
    },
    mounted() {
      Axios.get("/static/data/musicdata.json")
      .then(res => {
        let list = res.data.musicData;
        list.forEach(element => {
          this.songs.push({
            title: element.title,
            author: element.author,
            url: element.src,
            pic: element.musicImgSrc,
            lrc: element.lrc
            /** '/static/lrc/假如爱有天意.lrc' */
          });
        });
        this.isShow = true;
      })
      .catch();
  },
  props: ['showLrc']
};
</script>

<style scoped>
.albums {
  margin: 1.2rem 0;
}
</style>



